'effectsActions.js'
import * as types from './effectsConsts';
import {effectGeneric} from './effectsConsts';
import {objNormalize} from '../js/obj_processing';
import {objKeysToArr} from '../Helpers';

export function loadEffects(effects){
  return{
    type: types.LOAD_EFFECTS,
    payload:{effects: objNormalize(objKeysToArr(effects),effectGeneric())}
  }
}

export function loadAdditionalEffects(effects){
  return{
    type: types.LOAD_ADD_EFFECTS,
    payload:{effects: objNormalize(objKeysToArr(effects),effectGeneric())}
  }
}

export function unloadEffects(){
  return{
    type: types.UNLOAD_EFFECTS,
    payload: {}
  }
}

export function createEffect(effect){
  return{
    type: types.CREATE_EFFECT,
    payload: {effect}
  }
}

export function removeEffect(effectId){
  return{
    type: types.REMOVE_EFFECT,
    payload:{
      effectId
    }
  }
}

export function removeEffects(effects){
  return{
    type: types.REMOVE_EFFECTS,
    payload:{
      effects
    }
  }
}

export function addSoundToEffect(soundId, effectId){
  return{
    type: types.ADD_SOUND_TO_EFFECT,
    payload:{
      effectId,
      soundId
    }
  }
}

export function moveSound(effectId, oldIndex, newIndex){
  return{
    type: types.MOVE_SOUND,
    payload:{
      effectId,
      oldIndex,
      newIndex
    }
  }
}

export function removeSoundReference(effectId, soundId){
  return{
    type: types.REMOVE_SOUND_REFERENCE,
    payload:{
      effectId,
      soundId
    }
  }
}

export function addSoundReference(effectId, soundId, index){
  return{
    type: types.ADD_SOUND_REFERENCE,
    payload:{
      effectId,
      soundId,
      index
    }
  }
}

export function changeEffectVar(effectId, varName, value, extras = null){
  return{
    type: types.CHANGE_EFFECT_VAR,
    payload:{
      effectId,
      varName,
      value,
      extras
    }
  }
}



