import React, {Component} from 'react';

class HowToVideo extends Component{
  render(){

    const video_1 = () =>{
      return(
        <div>
          <div className="head">
            <h1>1. The Basics</h1>
          </div>
          <div className="body">
          <p>This video is a quick run down on the basic features of this app. This simple example will who you how to make a soundscape for a flooded city.</p>

          </div>
        </div>
      )
    }

    const video_2 = () =>{
      return(
        <div>
          <div className="head">
            <h1>Advanced Features</h1>
          </div>
          <div className="body">
            <p>This video walks through some of the advanced features of this app.</p>
          </div>
        </div>
      )
    }

    const content = () =>{
      switch(this.props.videoIndex){
        case 0:
          return video_1();
        case 1:
          return video_2();
        default:
          break;
      }
    }
    return(
      <div className="how-to-video">
        {content()}
      </div>
    )
  }
}

export default HowToVideo;



