import React, {Component} from 'react';

class ModalBase extends Component{
  render(){
    return(
      <div className="modal-wrapper">
        <div className="modal-background"></div>
        <div className={"_modal " + this.props.classes}>
          <div className="_modal-header dark"><p>{this.props.title}</p></div>
          {this.props.children}
          {typeof this.props.close !== 'undefined' &&
            <button onClick={this.props.close}>Close</button>
          }
          {typeof this.props.confirm !== 'undefined' &&
            <button onClick={this.props.confirm}>Confirm</button>
          }
        </div>
      </div>
    );
  }
}

export default ModalBase;



