import React, {Component} from 'react';

import SoundLibrary from '../components/soundLibrary';
import soundTree from '../sounds/soundsFormatted';
import ReactHowler from 'react-howler';
import {SidebarStates} from '../constants/constants';

class SoundLibraryContainer extends Component{
  constructor(props){
    super(props);
    this.state = {
      sounds: soundTree,
      opened: {},
      playTimer: null,
      filePath: "",
      playing: false
    }

    this.soundClicked = this.soundClicked.bind(this);
    this.folderClicked = this.folderClicked.bind(this);
    this.playSound = this.playSound.bind(this);
    this.stopSound = this.stopSound.bind(this);
    this.toggleSound = this.toggleSound.bind(this);
  }

  componentWillUnmount(){
    const {playTimer} = this.state;
    if(playTimer !== null){
      clearTimeout(playTimer);
    }
  }

  folderClicked(path){

    let _opened = Object.assign({},this.state.opened);
    if(typeof _opened[path] !== 'undefined'){
      delete _opened[path];
    }else{
      _opened[path] = true;
    }
    this.setState({opened:_opened});
  }

  playSound(){
    this.setState({
      playing: true
    });
  }

  stopSound(){
    this.setState({
      playing:false
    });
  }

  toggleSound(){
    const _playing = !this.state.playing;
    if(_playing){
      this.player.seek(0);
    }
    this.setState({
      playing: _playing
    });
  }

  soundClicked(item){
    if(this.state.filePath === item.path){
      return this.toggleSound();
    }
    if(this.state.playTimer === null){
      const playTimer = setTimeout(()=>{
        this.setState({playTimer:null});
      },1000);
      this.setState({
        filePath:item.path,
        playing: true,
        playTimer: playTimer
      });
    }
  }

  render(){
    return (
      <div>
        <SoundLibrary
        classes={this.props.classes}
        sounds={this.state.sounds}
        opened={this.state.opened}
        playing={this.state.playing}
        filePath={this.state.filePath}
        stopPlaying={()=>this.setState({playing:false})}
        sidebarMain={()=>this.props.changeSidebarState(SidebarStates.MAIN)}
        folderClicked={(path)=>this.folderClicked(path)}
        soundClicked={this.soundClicked}/>
        <ReactHowler
        src={"./sounds/"+this.state.filePath}
        playing={this.state.playing}
        loop={false}
        html5={true}
        preload={false}
        onEnd={this.stopSound}
        ref={(ref)=> (this.player = ref)}/>
      </div>
    )
  }
}

export default SoundLibraryContainer;



