import * as types from './collectionsConsts';
import {collectionBuilder, collectionGeneric} from './collectionsConsts';
import {objNormalize} from '../js/obj_processing';
import {objKeysToArr} from '../Helpers';

export function loadCollections(collections){
  console.log(collections);
  return{
    type: types.LOAD_COLLECTIONS,
    payload: {collections: objNormalize(objKeysToArr(collections),collectionGeneric())}
  }
}

export function loadAdditionalCollections(collections){
  return{
    type: types.LOAD_ADD_COLLECTIONS,
    payload: {collections: objNormalize(objKeysToArr(collections),collectionGeneric())}
  }
}

export function unloadCollections(){
  return{
    type: types.UNLOAD_COLLECTIONS,
    payload: {}
  }
}

export function addCollection(name = "New Collection"){
  const collection = collectionBuilder(name);
  return{
    type: types.ADD_COLLECTION,
    payload: {collection}
  }
}

export function removeCollection(collectionId){
  return{
    type: types.REMOVE_COLLECTION,
    payload:{
      collectionId
    }
  }
}

export function moveCollection(collectionId, direction){
  return{
    type: types.MOVE_COLLECTION,
    payload:{
      collectionId,
      direction
    }
  }
}

export function addEffectToCollection(effectId, collectionId){
  return{
    type: types.ADD_EFFECT_TO_COLLECTION,
    payload:{
      collectionId,
      effectId
    }
  }
}
export function moveEffectInCollection(effectId,collectionId,direction){
  return{
    type: types.MOVE_EFFECT_IN_COLLECTION,
    payload:{
      collectionId,
      effectId,
      direction
    }
  }
}
export function moveEffectToCollection(oldCollectionId, newCollectionId, effectId){
  return{
    type: types.MOVE_EFFECT_TO_COLLECTION,
    payload:{
      oldCollectionId,
      newCollectionId,
      effectId
    }
  }
}
export function removeEffectFromCollection(effectId, collectionId){
  return{
    type: types.REMOVE_EFFECT_FROM_COLLECTION,
    payload:{
      collectionId,
      effectId
    }
  }
}
export function removeEffectsFromCollection(collectionId){
  return{
    type: types.REMOVE_EFFECTS_FROM_COLLECTION,
    payload:{
      collectionId
    }
  }
}
export function changeCollectionVar(id,varName,value){
  return{
    type: types.CHANGE_COLLECTION_VAR,
    payload:{
      id,
      varName,
      value
    }
  }
}



