export const LOAD_APP = "LOAD_APP";
export const CHANGE_APP_VAR = "CHANGE_APP_VAR";

export function appGeneric(){
  return {
    name: "SoundBoard",
    volume: 100,
    advanced: true,
    loading: false
  };
}

export function appBuilder(){
  let app = appGeneric();
  return app;
}



