import React from 'react';
import {DropTarget} from 'react-dnd';

export const CollectionContainerDropTarget = ({connectDropTarget, children}) => {
  return connectDropTarget(
    <div className="collection-container-droptarget">
      {children}
    </div>
  )
}

export default DropTarget(
  "DEFAULT",
  {
    drop(props,monitor){
      props.onDrop(monitor.getItem());
    },
  },
  (connect, monitor) => ({
    connectDropTarget: connect.dropTarget()
  }),
)(CollectionContainerDropTarget);



