import {objKeyArray} from '../Helpers';

export function objNormalize(obj,objTemplate){

  if(Array.isArray(obj)){
    let o = {};
    obj.forEach((item,i)=>{
      const newObj = objDeleteUnrepresented(Object.assign({},objTemplate,item),objTemplate);
      o[newObj.id] = newObj;
    });
    return o;
  }

  return objDeleteUnrepresented(Object.assign({},objTemplate,obj),objTemplate);
}

export function objDeleteUnrepresented(object, objTemplate){

  const obj = Object.assign({},object);
  const objStructure = objKeyArray(obj);

  objStructure.forEach((key)=>{
    if(typeof objTemplate[key] === 'undefined'){
      delete obj[key];
    }
  });

  return obj;
}



