import React, {Component} from 'react';

class HowTo extends Component{
  render(){
    const isSelected = (i) =>{
      if(i === this.props.videoIndex){
        return " selected";
      }
      return "";
    }
    return(
      <div className={"sidebar sidebar-howto dark " + this.props.classes}>
        <div className="sidebar-head">
          <button type="button" name="button" className="back" onClick={this.props.sidebarMain}><i className="fas fa-angle-left"></i></button>
          <h5>How To Use</h5>
        </div>
        <div className="sidebar-content">
          <ul className="no-style">
            <li className={isSelected(0)}>
              <div>
                <a onClick={()=>this.props.changeVideoIndex(0)}><span>1.</span>The Basics</a>
              </div>
            </li>
            <li className={isSelected(1)}>
              <div>
                <a onClick={()=>this.props.changeVideoIndex(1)}><span>2.</span>Advanced Use</a>
              </div>
            </li>
          </ul>
        </div>
      </div>
    );
  }
}

export default HowTo;



