import React, {Component} from 'react';
import {connect} from 'react-redux';

import Collection from '../components/collection';
import {addCollection, removeCollection, moveCollection, changeCollectionVar, moveEffectToCollection} from '../actions/collectionsActions';

import {removeEffects} from '../actions/effectsActions';
import {removeSounds} from '../actions/soundsActions';
import {objKeysToArr} from '../Helpers'
import {SaveReminderValues} from '../constants/constants';
class CollectionContainer extends Component{
  constructor(props){
    super(props);

    this.mediaButtonRef = this.mediaButtonRef.bind(this);
    this.playAllClicked = this.playAllClicked.bind(this);
    this.pauseAllClicked = this.pauseAllClicked.bind(this);

    this.getEffects = this.getEffects.bind(this);
    this.state = {
      mediaButtons : {}
    }
  }

  componentDidUpdate(){
    //console.log(this.state);
  }


  //Function for passing reference to effect media buttons up from children
  //In order to play sounds from sibling container
  mediaButtonRef(collectionId){
    //Function returns an object with 2 functions
    return {
      //Sets the reference to the play and pause buttons in the state
      setRef:(play, pause, id)=>{
        let ref = {
          "play":play,
          "pause":pause
        };

        this.setState((prevState) =>{
          let mb;
          if(typeof prevState.mediaButtons[collectionId] === 'undefined'){
            mb = {[collectionId]:{[id]:ref}};
          }else{
            mb = {[collectionId]:Object.assign(prevState.mediaButtons[collectionId],{[id]:ref})};
          }
          return {mediaButtons: Object.assign(mb,prevState.mediaButtons)};
        });
      },

      //Deletes the reference from the state on unload
      removeRef:(id)=>{
        //console.log(`Removing ${id} from ${collectionId}`);
        this.setState((prevState)=>{
          let mb = Object.assign({},prevState.mediaButtons[collectionId]);
          delete mb[id];
          return{mediaButtons:Object.assign(prevState.mediaButtons,{[collectionId]:mb})}
        });
      }
    };
  }

  playAllClicked(collectionId){
    const arr = objKeysToArr(this.state.mediaButtons[collectionId]);
    arr.forEach((i)=>{
      i.play();
    });
  }
  pauseAllClicked(collectionId){
    const arr = objKeysToArr(this.state.mediaButtons[collectionId]);
    arr.forEach((i)=>{
      i.pause();
    });
  }

  getEffects(effectsArr){
    const {effects} = this.props;
    let effectObj = {};
    effectsArr.forEach((id)=>{
      effectObj[id] = effects[id];
    });
    return effectObj;
  }

  render(){

    const collections = objKeysToArr(this.props.collections).map((collection,i)=>{
      let allSounds = [];
      collection.effects.forEach((effect)=>{
        allSounds.push.apply(allSounds,this.props.effects[effect].sounds);
      });
      const remove = {remove:()=>this.props.removeCollection(collection.id)}
      const cleanUp = {cleanUp:()=>this.props.cleanUp(collection.effects, allSounds)}
      const changeVar = {changeVar:(varName, value)=>this.props.changeVar(collection.id,varName,value)}
      const moveEffect = {moveEffect:(oldCollectionId, effectId)=>this.props.moveEffectToCollection(oldCollectionId,collection.id,effectId)}
      const c = Object.assign({},collection,remove, cleanUp, changeVar, moveEffect);
      const effects = this.getEffects(collection.effects);
      return(
        <Collection collection={c}
        effects={effects}
        mediaButtonRef={()=>this.mediaButtonRef(c.id)}
        playAll={()=>this.playAllClicked(c.id)}
        pauseAll={()=>this.pauseAllClicked(c.id)}
        key={collection.id}
        addSaveReminderValue={(value)=>this.props.addSaveReminderValue(value)}></Collection>
      )
    });

    return(
      <div className="builder-content">
        {collections}
        <div className="new-collection" onClick={()=>{this.props.addCollection("New Collection");this.props.addSaveReminderValue(SaveReminderValues.SMALLEDIT)}}>
          <p><i className="fas fa-plus mr-2"></i>New Collection</p>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) =>{
  return{
    collections: state.collections,
    effects: state.effects
  }
}

const mapDispatchToProps = (dispatch) =>{
  return {
    addCollection: (name) =>{
      dispatch(addCollection(name));
    },
    removeCollection:(collectionId)=>{
      dispatch(removeCollection(collectionId));
    },
    moveCollection:(collectionId, direction)=>{
      dispatch(moveCollection(collectionId,direction));
    },
    moveEffectToCollection:(oldCollectionId, newCollectionId, effectId)=>{
      dispatch(moveEffectToCollection(oldCollectionId,newCollectionId,effectId));
    },
    cleanUp:(effects, sounds)=>{
      dispatch((dispatch)=>{
        dispatch(removeEffects(effects));
        dispatch(removeSounds(sounds))
      });
    },
    changeVar:(id, varName, value)=>{
      dispatch(changeCollectionVar(id,varName,value));
    }
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(CollectionContainer);



