import React, {Component} from 'react';

import SoundsContainer from '../containers/SoundsContainer';
import {EffectStyles} from '../actions/effectsConsts';
import {isKeypressEnter} from '../Helpers';
import EffectDragSource from './dragdrop/EffectDragSource';
import {SaveReminderValues} from '../constants/constants';

class Effect extends Component {
  constructor(props){
    super(props);

    this.state = {
      name: this.props.effect.name,
      menu: false,
      editingName: false,
      delayTimer: null
    }

    this.changeName = this.changeName.bind(this);
    this.menuToggle = this.menuToggle.bind(this);
    this.nameInputBlur = this.nameInputBlur.bind(this);
    this.nameInputChange = this.nameInputChange.bind(this);
  }

  componentDidUpdate(prevProps, prevState, snapshot){
    if(prevState.editingName === false && this.state.editingName){
      this.setState({name:this.props.effect.name});
      this.editNameInput.focus();
    }
  }
  componentWillUnmount(){
    window.clearTimeout(this.state.delayTimer);
    this.props.mediaButtonRef().removeRef(this.props.effect.id);
  }

  menuToggle(){
    this.setState({menu: !this.state.menu})
  }

  changeName(){
    const name = this.editNameInput.value.trim();
    if(name !== ""){
      this.props.effect.changeVar('name', name);
      this.props.addSaveReminderValue(SaveReminderValues.SMALLEDIT);
    }
    this.setState({editingName: false});
  }
  nameInputChange(e){
    this.setState({name:e.target.value});
  }
  nameInputBlur(){
    const delayTimer = window.setTimeout(()=>{
      this.setState({editingName:false});
    },200);
    this.setState({delayTimer:delayTimer});
  }

  render() {
    const {effect} = this.props;
    const name = () =>{
      if(this.state.editingName){
        return (
          <div>
            <input type="text" onKeyPress={(e)=>isKeypressEnter(e,()=>this.changeName())} ref={(input) => {this.editNameInput = input}} onChange={this.nameInputChange} value={this.state.name} onBlur={this.nameInputBlur}></input>
            <i className="fas fa-check" onClick={this.changeName} onFocus={()=>this.setState({editingName:true})}></i>
          </div>
        );
      }
      return <p onClick={()=>this.setState({editingName:true})}>{effect.name}</p>;
    }
    const minimized = () =>{
      if(effect.minimized){
        return " minimized";
      }
      return "";
    }
    return (
      <div className={"effect-card" + minimized()}>
        <div className="head">
          <EffectDragSource classes="effect-drag-point" effectId={effect.id} collectionId={this.props.collectionId}/>
          <i className="fas fa-ellipsis-v" onClick={this.menuToggle}></i>
          <div className="menu d-none">
            <a onClick={effect.loopAll}>Loop All Sounds</a>
            <a onClick={effect.fadeAll}>Fade All Sounds</a>
          </div>
          <div className="flex-right">
            <select className="effect-card-select" onChange={(e)=>{effect.changeStyle(e.target.value)}} defaultValue={effect.style}>
              <option value={EffectStyles.DEFAULT}>Soundboard</option>
              <option value={EffectStyles.RANDOM}>Random</option>
              <option value={EffectStyles.SEQUENTIAL}>Sequential</option>
            </select>
            <i className="fas fa-times delete" onClick={effect.remove}></i>
          </div>
        </div>
        <div className="effect-content">
          <div className="name">
            {name()}
          </div>
          <SoundsContainer masterVolume={this.props.masterVolume} effect={effect} setRef={(play,pause)=>this.props.mediaButtonRef().setRef(play,pause,effect.id)} addSaveReminderValue={(value)=>this.props.addSaveReminderValue(value)}/>
        </div>
        <div className="effect-minimize">
          <hr className={effect.minimized ? "" : "d-none"}/>
          <div className="wrapper">
            <i className="fas fa-chevron-left shift" onClick={()=>effect.moveLocal(-1)}></i>
            <i className={effect.minimized ? "fas fa-chevron-down" : "fas fa-chevron-up"} onClick={()=>effect.changeVar("minimized",!effect.minimized)}></i>
            <i className="fas fa-chevron-right shift" onClick={()=>effect.moveLocal(1)}></i>
          </div>
        </div>
      </div>);
  }
}

export default Effect;



