import React, {Component} from 'react';

import HowToVideo from '../components/howToVideo';

class HowToVideoContainer extends Component{

  render(){
    return(
      <HowToVideo videoIndex={this.props.videoIndex}/>
    );
  }
}

export default HowToVideoContainer;



