import React, {Component} from 'react';

class About extends Component{
  render(){
    return(
      <div className={"sidebar sidebar-about dark " + this.props.classes}>
        <div className="sidebar-head" >
          <button type="button" name="button" className="back" onClick={this.props.sidebarMain}><i className="fas fa-angle-left"></i></button>
          <h5>About</h5>
        </div>
        <div className="sidebar-content about">
          <p>Hey! Thanks for checking out this app. Below is real quick rundown of what this app is and what it can do.</p>
          <p>Make sure to check out the "How To Use" section if you want some pointers on how to work this app.</p>
          <hr/>
          <ul className="no-style">
            <li>
              <i className="fas fa-hammer"></i>
              <p>Build your own soundscapes using your own sound files, or sounds we provide!</p>
              <hr/>
            </li>
            <li>
              <i className="fas fa-file-export"></i>
              <p>Export and download your completed soundscapes and take them anywhere!</p>
              <hr/>
            </li>
            <li>
              <i className="fas fa-mobile-alt"></i>
              <p>Mobile ready! (sorta)</p>
              <hr/>
            </li>
            <li className="urgent">
              <i className="fas fa-exclamation"></i>
              <p>This application does not autosave yet, make sure to save your soundscape often!</p>
              <hr/>
            </li>
            <li className="urgent">
              <i className="fas fa-exclamation"></i>
              <p>This application is in alpha and is not bug free, please save often!</p>
              <hr/>
            </li>
          </ul>
        </div>
      </div>
    );
  }
}

export default About;



