import React, {Component} from 'react';
import HowTo from '../components/howTo';
import {SidebarStates} from '../constants/constants';

class HowToContainer extends Component{
  render(){
    return(
      <HowTo
      classes={this.props.classes}
      sidebarMain={()=>this.props.changeSidebarState(SidebarStates.MAIN)}
      changeVideoIndex={(i)=>this.props.changeVideoIndex(i)}
      videoIndex={this.props.videoIndex}/>
    )
  }
}

export default HowToContainer;



