import React, {Component} from 'react';

class SaveReminderModal extends Component{

  render(){
    return(
      <div>
        <p>It looks like you've made some changes, and it's been a while since you've saved.</p>
        <h5>Would you like to save now?</h5>
        <button className="mxr-button-accent-solid" onClick={this.props.confirm}>Save Now</button>
        <button className="mxr-button-red-borderless" onClick={this.props.close}>No I'm Good</button>
      </div>
    )
  }
}

export default SaveReminderModal;



