import UUID from 'uuid/v4';

export const LOAD_COLLECTIONS = "LOAD_COLLECTIONS";
export const LOAD_ADD_COLLECTIONS = "LOAD_ADD_COLLECTIONS";
export const UNLOAD_COLLECTIONS = "UNLOAD_COLLECTIONS";
export const ADD_COLLECTION = "ADD_COLLECTION";
export const REMOVE_COLLECTION = "REMOVE_COLLECTION";
export const MOVE_COLLECTION = "MOVE_COLLECTION";
export const ADD_EFFECT_TO_COLLECTION = "ADD_EFFECT_TO_COLLECTION";
export const MOVE_EFFECT_IN_COLLECTION = "MOVE_EFFECT_IN_COLLECTION";
export const MOVE_EFFECT_TO_COLLECTION = "MOVE_EFFECT_TO_COLLECTION";
export const REMOVE_EFFECT_FROM_COLLECTION = "REMOVE_EFFECT_FROM_COLLECTION";
export const REMOVE_EFFECTS_FROM_COLLECTION = "REMOVE_EFFECTS_FROM_COLLECTION";
export const CHANGE_COLLECTION_VAR = "CHANGE_COLLECTION_VAR";

export function collectionGeneric(){
  return{
    id: "",
    name: "",
    volume: 100,
    mute: false,
    minimized: false,
    effects:[]
  };
}

export function collectionBuilder(name){
  let collection = collectionGeneric();
  collection.id = UUID();
  collection.name = name;

  return collection;
}

export function getInitialCollectionState(){
  let collection = collectionBuilder("New Collection");
  return {[collection.id]:collection};
}



