import React, {Component} from 'react';
import {createPortal} from 'react-dom';
import ModalBase from '../components/modalBase';

const modalRoot = document.getElementById('modal-root');

class ModalBaseContainer extends Component{
  constructor(props){
    super(props);
    this.el = document.createElement('div');
  }
  componentDidMount(){
    modalRoot.appendChild(this.el);
  }
  componentWillUnmount(){
    modalRoot.removeChild(this.el);
  }
  render(){
    return createPortal(
        <ModalBase close={this.props.close} confirm={this.props.confirm} title={this.props.title} classes={this.props.classes}>{this.props.children}</ModalBase>,
        this.el
    )
  }
}
export default ModalBaseContainer;



