import UUID from 'uuid/v4'

export const LOAD_EFFECTS = "LOAD_EFFECTS";
export const LOAD_ADD_EFFECTS = "LOAD_ADD_EFFECTS";
export const UNLOAD_EFFECTS = "UNLOAD_EFFECTS";
export const CREATE_EFFECT = "CREATE_EFFECT";
export const REMOVE_EFFECT = "REMOVE_EFFECT";
export const REMOVE_EFFECTS = "REMOVE_EFFECTS";
export const ADD_SOUND_TO_EFFECT = "ADD_SOUND_TO_EFFECT";
export const MOVE_SOUND = "MOVE_SOUND_EFFECT";
export const REMOVE_SOUND_REFERENCE = "REMOVE_SOUND_REFERENCE";
export const ADD_SOUND_REFERENCE = "ADD_SOUND_REFERENCE";
export const CHANGE_EFFECT_VAR = "CHANGE_EFFECT_VAR";

export const EffectStyles = Object.freeze({
  DEFAULT: "default",
  RANDOM: "random",
  SEQUENTIAL: "sequential"
});

export function effectGeneric(){
  return{
    id: "",
    name: "",
    volume: 100,
    mute: false,
    style: EffectStyles.DEFAULT,
    randomLoop: false,
    sounds: [],
    minimized: false,
    delay: false,
    delayMin: 1,
    delayMax: 4
  };
}

export function effectBuilder(name){
  let effect = effectGeneric();
  effect.id = UUID();
  effect.name = name;

  return effect;
}



