import React, {Component} from 'react';
import ReactHowler from 'react-howler';
import {volumeSliderStyle} from '../js/input';

import {EffectStyles} from '../actions/effectsConsts'
import SoundDragTarget from './dragdrop/SoundDragTarget';

class Sound extends Component{
  constructor(props){
    super(props);

    this.state = {
      name: this.props.sound.name,
      editingName: false
    }

    this.nameInputKeypress = this.nameInputKeypress.bind(this);
    this.renderProgress = this.renderProgress.bind(this);
    this.changeSeek = this.changeSeek.bind(this);
    this.changeName= this.changeName.bind(this);
  }
  //Interval on component mount for rendering the progress bar.
  componentDidMount(){
    this.timer = setInterval(this.renderProgress,300);
    this.props.passUpPlayerRef(this.player);
  }

  componentDidUpdate(prevProps,prevState,snapshot){
    if(prevState.editingName === false && this.state.editingName){
      this.setState({name:this.props.sound.name});
      this.editNameInput.focus();
    }
  }

  //Clears the interval when the component unmounts.
  componentWillUnmount(){
    this.props.effect.removeSoundPlaying(this.props.sound.id);
    clearInterval(this.timer);
  }

  //Function for forcing updated of component to rerender the progress bar.
  renderProgress(){
    if(this.props.sound.playing){
      this.forceUpdate();
    }
  }

  //Checks for enter-key press when editing sound name
  nameInputKeypress(e){
    //Gets the keycode, if code is 13 (enter), it processes the name change
    const code = (e.keyCode ? e.keyCode : e.which);
    if(code === 13){
      this.changeName();
    }
  }

  changeSeek(e){
    const seek = (this.player.duration() * (e.target.value / 100));
    console.log(seek);
    this.player.seek(seek);
    this.forceUpdate();
  }

  changeName(){
    const {sound} = this.props;
    const name = this.editNameInput.value.trim();
    if(name !== ""){
      sound.changeName(name);
    }
    this.setState({editingName: false});
  }

  render(){
    const {sound,effect} = this.props;

    /////////////////////////////
    // Minor or Computational //
    ///////////////////////////

    //The progress bar styles
    const progress = () =>{
      //Determines if the player is loaded, if it is, the position of the progress bar is updated.
      if( typeof this.player  !== 'undefined' && this.player.howlerState() === 'loaded'){
        const x = (this.player.seek() / this.player.duration()) * 100;
        return {width: x+"%"}
      }
      return {};
    }

    const seekValue = () =>{
      //Determines if the player is loaded, if it is, the position of the progress bar is updated.
      if( typeof this.player  !== 'undefined' && this.player.howlerState() === 'loaded'){
        return (this.player.seek() / this.player.duration()) * 100;
      }
      return 0;
    }

    //Calculates the volume level
    //Uses the level of the sound, the overall effect, and the sound's fade variable
    //To obtain a float between 0 - 1 that represents the final volume level
    const vol = this.props.masterVolume * (effect.volume/100) * (sound.volume/100);

    //Updates the volume styles
    const volStyles = () =>{
      return volumeSliderStyle(sound.volume);
    }

    //Returns different volume icon and classes dependent on certain factors.
    const volIcon = () =>{
      //If the sound has been muted
      if(sound.mute || sound.volume === 0){return "fas fa-volume-mute icon muted";}
      //If the sound is muted by another sound's solo
      if(sound.soloMute){return "fas fa-volume-muted icon";}
      //Volume icon based on volume level
      if(sound.volume > 50) {return "fas fa-volume-up icon";}
      else {return "fas fa-volume-down icon";}
    }

    /////////////////
    // Structural //
    ///////////////

    //Name of the sounds
    const name = () => {
      if(this.state.editingName){
        return(
          <div className="name">
            <input type="text" className="name" onKeyPress={this.nameInputKeypress} ref={(input) => {this.editNameInput = input}} onChange={(e)=>this.setState({name:e.target.value})} onBlur={()=> this.setState({editingName:false})} value={this.state.name}></input>
            <i className="fas fa-check"  onClick={this.changeName} onFocus={()=>this.setState({editingName:true})}></i>
          </div>
        );
      }
      return (
        <div className="name">
          <p className="name" onClick={()=>this.setState({editingName:true})}>{sound.name}</p>
        </div>);
    }

    //Media button function
    const mediaButtons = () =>{
      const PlayButton = <i className="fas fa-play" onClick={sound.play} key="play"></i>;
      const PauseButton = <i className="fas fa-pause" onClick={sound.pause} key="pause"></i>;
      const StopButton = <i className="fas fa-stop" onClick={sound.stop} key="stop"></i>;
      const LoopButton = <i className={sound.loop ? "fas fa-undo" : "fas fa-undo inactive"} onClick={sound.loopToggle} key="undo"></i>;
      //const SoloButton = <i className={sound.solo ? "" : "inactive"} onClick={sound.soloToggle} key="solo">Solo</i>

      let buttons = [];
      switch(effect.style){
        case EffectStyles.DEFAULT:{
          if(sound.playing){buttons.push(PauseButton);}
          else{buttons.push(PlayButton);}
          buttons.push(StopButton);
          buttons.push(LoopButton);
          break;
        }
        case EffectStyles.SEQUENTIAL:{
          buttons.push(LoopButton);
          break;
        }
        default:{return;}
      }
      return(
        <div className="media-buttons">
          {buttons}
        </div>
      );
    }

    const volume = () =>{
      return(
        <div className="volume">
          <i className={"volume-icon " + volIcon()} onClick={sound.muteToggle}></i>
          <input className="volume-input volume-input-sound" type="range"  min="0" max="100" value={sound.volume} onChange={(e)=>sound.adjustVolume(e.target.value)}/>
          <div className="volume-track" style={volStyles()}></div>
        </div>
      )
    }

    const seekBar = () =>{
      switch(effect.style){
        case EffectStyles.DEFAULT:
        case EffectStyles.SEQUENTIAL:{
          return(
            <div className="sound-seek-bar">
              <div className="seek-progress-bar" style={progress()}></div>
              <div className="wrapper">
                <input type="range" min="0" max="100" value={seekValue()} onChange={(e)=>this.changeSeek(e)}/>
              </div>
              <div className="seek-track"></div>
            </div>
          );
        }
        default:{return;}
      }
    }

    ///////////////////////
    // Howler Variables //
    /////////////////////

    const loop = () =>{
      if(sound.loop){
        switch(effect.style){
          case EffectStyles.DEFAULT:
            return true;
          case EffectStyles.RANDOM:
            return false;
          case EffectStyles.SEQUENTIAL:{
            if(sound.passover){
              return false;
            }
            return true;
          }
          default:
            return true;
        }
      }
      return false;
    }

    return(
      <div className={sound.playing ? "sound-card playing" : "sound-card"}>
        <SoundDragTarget key={sound.id} soundId={sound.id} effectId={effect.id} classes="sound-drag-point"></SoundDragTarget>
        <div className="sound-head">
          <i className="fas fa-ellipsis-v"></i>
          <i className="fas fa-times flex-right sound-close" onClick={sound.remove}></i>
        </div>
        <div className="sound-content">
          {name()}
          <div className="sound-controls">
            {mediaButtons()}
            {volume()}
          </div>
        </div>
        {seekBar()}
        <ReactHowler
          src={sound.url}
          format={[sound.ext]}
          playing={sound.playing}
          volume={vol}
          mute={sound.mute || effect.mute || sound.soloMute}
          loop={loop()}
          html5={false}
          preload={false}
          onLoad={sound.onLoad}
          onEnd={()=>sound.onFinish(this.player)}
          ref={(ref) => (this.player = ref)}/>
      </div>
    );
  }
}

export default Sound;



