import React, {Component} from 'react';
import AppHeader from '../components/appHeader';

import {isKeypressEnter} from '../Helpers';

class AppHeaderContainer extends Component{
  constructor(props){
    super(props);

    this.changeName = this.changeName.bind(this);
    this.nameInputBlur = this.nameInputBlur.bind(this);
    this.nameInputChanged = this.nameInputChanged.bind(this);
    this.nameInputKeyPress = this.nameInputKeyPress.bind(this);
    this.setInputRef = this.setInputRef.bind(this);

    this.state = {
      name: this.props.name,
      editingName: false,
      nameInputBlurDelayTimer: null
    }
  }

  componentWillUnmount(){
    const {nameInputBlurDelayTimer} = this.state;
    if(nameInputBlurDelayTimer !== null){
      clearTimeout(nameInputBlurDelayTimer);
      this.setState({nameInputBlurDelayTimer:null});
    }
  }

  changeName(){
    const name = this.inputRef.value.trim();
    if(name !== ""){
      this.props.changeAppVar("name",name);
    }
    this.setState({editingName:false})
  }
  nameInputChanged(e){
    this.setState({name:e.target.value})
  }
  nameInputBlur(){
    const delayTimer = window.setTimeout(()=>{
      this.setState({editingName:false});
    },200);
    this.setState({nameInputBlurDelayTimer:delayTimer});
  }
  nameInputKeyPress(e){
    if(isKeypressEnter(e)){
      this.changeName();
    }
  }

  setInputRef(ref){
    this.inputRef = ref;
  }

  render(){
    return(
      <AppHeader
        name={this.state.name}
        changeName={this.changeName}
        editingName={this.state.editingName}
        nameClicked={()=>this.setState({editingName:true})}
        nameInputKeyPress={this.nameInputKeyPress}
        nameInputRef={this.setInputRef}
        nameInputChanged={this.nameInputChanged}
        nameInputBlur={this.nameInputBlur}
      />
    )
  }
}

export default AppHeaderContainer;



