import React, { Component } from 'react';
import {connect} from 'react-redux';

import AppHeaderContainer from './containers/AppHeaderContainer';
import CollectionContainer from './containers/CollectionContainer';
import ControlsContainer from './containers/ControlsContainer';
import SoundLibraryContainer from './containers/SoundLibraryContainer';
import AboutContainer from './containers/AboutContainer';
import HowToContainer from './containers/HowToContainer';
import HowToVideoContainer from './containers/HowToVideoContainer';

import ModalBaseContainer from './containers/ModalBaseContainer';
import AddCollectionModalContainer from './containers/AddCollectionModalContainer';
import SaveReminderModalContainer from './containers/SaveReminderModalContainer';
import OptionsModalContainer from './containers/OptionsModalContainer';
//import About from './components/about';
import {loadCollections, unloadCollections, loadAdditionalCollections} from './actions/collectionsActions';
import {loadEffects, unloadEffects, loadAdditionalEffects} from './actions/effectsActions';
import {loadSounds, unloadSounds, loadAdditionalSounds} from './actions/soundsActions';
import {loadApp,changeAppVar} from './actions/appActions';

import {loadData, exportData} from './js/file_manager_new';
import {SidebarStates, ModalWindows, SaveReminderValues} from './constants/constants';

class App extends Component {
  constructor(props){
    super(props);

    this.changeModalState = this.changeModalState.bind(this);
    this.changeSidebarState = this.changeSidebarState.bind(this);
    this.changeHowToVideoIndex = this.changeHowToVideoIndex.bind(this);

    this.load = this.load.bind(this);
    this.add = this.add.bind(this);
    this.export = this.export.bind(this);

    this.addModalComplete = this.addModalComplete.bind(this);
    this.addModalCancel = this.addModalCancel.bind(this);
    this.sanitizeAddModalState = this.sanitizeAddModalState.bind(this);

    this.saveReminderCancel = this.saveReminderCancel.bind(this);
    this.saveReminderConfirm = this.saveReminderConfirm.bind(this);

    this.addSaveReminderValue = this.addSaveReminderValue.bind(this);
    this.checkSaveReminderValue = this.checkSaveReminderValue.bind(this);
    this.clearSaveReminderValue = this.clearSaveReminderValue.bind(this);
    this.clearSaveReminderTimer = this.clearSaveReminderTimer.bind(this);

    this.state = {
      sidebarState: SidebarStates.MAIN,
      howToVideoIndex: 0,
      saveReminderValue: 0,
      saveReminderTimer: null,
      modalWindows: {
        [ModalWindows.ADDSOUNDS]: false,
        [ModalWindows.SAVEREMINDER]: false,
        [ModalWindows.OPTIONS]: false
      },
      addModalData: {}
    }
  }

  componentWillUnmount(){
    this.clearSaveReminderTimer();
  }

  // File Handling
  load(e){
    loadData(e,{
      onLoad: () =>{
        this.props.changeAppVar('loading',true);
      },
      onFailure: ()=>{
        this.props.changeAppVar('loading',false);
      },
      onSuccess: (state) =>{
        this.props.unload();
        this.props.load(state.collections,state.effects,state.sounds,state.app);
      }
    });
  }
  add(e){
    loadData(e,{
      onLoad: ()=>{
      },
      onFailure: ()=>{
      },
      onSuccess: (state)=>{
        this.setState({addModalData:state},()=>this.changeModalState("ADD_SOUNDS",true));
      }
    }, this.props.state);
  }
  export(){
    exportData(this.props.state);
    this.clearSaveReminderValue();
  }

  // Sidebar Stuff
  changeSidebarState(newVal){
    let newState = "";
    switch(newVal){
      case SidebarStates.LIBRARY:
        newState = SidebarStates.LIBRARY;
        break;
      case SidebarStates.ABOUT:
        newState = SidebarStates.ABOUT;
        break;
      case SidebarStates.HOWTO:
        newState = SidebarStates.HOWTO;
        break;
      default:
        newState = SidebarStates.MAIN;
    }

    this.setState({
      sidebarState : newState
    });
  }
  changeHowToVideoIndex(newIndex){
    this.setState({
      howToVideoIndex: newIndex
    });
  }

  // Modal Stuff
  changeModalState(modal,value){
    if(typeof this.state.modalWindows[modal] !== undefined){
      const modalWindows = Object.assign({},this.state.modalWindows,{[modal]:value})
      this.setState({modalWindows:modalWindows});
    }
  }

  // Add Modal
  addModalComplete(data){
    this.props.add(data.c,data.e,data.s);
    this.sanitizeAddModalState();
  }
  addModalCancel(){
    this.sanitizeAddModalState();
  }
  sanitizeAddModalState(){
    this.setState({addModalData:{}},()=>this.changeModalState(ModalWindows.ADDSOUNDS,false));
  }

  //Save Reminder Modal
  saveReminderConfirm(){
    exportData(this.props.state);
    this.clearSaveReminderTimer();
    this.setState({saveReminderValue:0});
    this.changeModalState(ModalWindows.SAVEREMINDER,false);
  }
  saveReminderCancel(){
    this.changeModalState(ModalWindows.SAVEREMINDER,false);
  }
  addSaveReminderValue(value){
    this.setState({saveReminderValue: this.state.saveReminderValue + value},()=>this.checkSaveReminderValue());
  }
  checkSaveReminderValue(){
    if(this.state.saveReminderTimer !== null){return;}
    if(this.state.saveReminderValue > SaveReminderValues.MAXVALUE){
      const saveReminderTimer = setTimeout(()=>{
        this.setState({saveReminderTimer:null},()=>this.changeModalState(ModalWindows.SAVEREMINDER, true));
      },SaveReminderValues.TIMER);
      this.setState({saveReminderTimer});
    }
  }
  clearSaveReminderValue(){
    this.clearSaveReminderTimer();
    this.setState({saveReminderValue:0});
  }
  clearSaveReminderTimer(){
    const {saveReminderTimer} = this.state;
    if(saveReminderTimer !== null){
        clearTimeout(saveReminderTimer);
        this.setState({saveReminderTimer:null})
    }
  }

  render() {

    const modalHandler = () =>{
      const {modalWindows} = this.state;
      if(modalWindows[ModalWindows.ADDSOUNDS]){
        return(
          <ModalBaseContainer title={"Add To Current Collection"} classes={"modal-add-collection"}>
            <AddCollectionModalContainer data={this.state.addModalData} close={this.addModalCancel} confirm={(data)=>this.addModalComplete(data)}/>
          </ModalBaseContainer>
        )
      }

      if(modalWindows[ModalWindows.OPTIONS]){
        return(
          <ModalBaseContainer title="Options" classes="modal-options">
            <OptionsModalContainer confirm={()=>this.changeModalState(ModalWindows.OPTIONS,false)}/>
          </ModalBaseContainer>
        )
      }

      if(modalWindows[ModalWindows.SAVEREMINDER]){
        return (
          <ModalBaseContainer title="Would You Like To Save?" classes="modal-save-reminder">
            <SaveReminderModalContainer confirm={this.saveReminderConfirm} close={this.saveReminderCancel}/>
          </ModalBaseContainer>
        )
      }
    }

    const sidebar = () =>{
      const fileHandler = {
        Load: this.load,
        Add: this.add,
        Export: this.export
      }
      return (
        <div>
          <ControlsContainer
            changeAppVar={this.props.changeAppVar}
            changeSidebarState={(v) => this.changeSidebarState(v)}
            clearSaveReminderValue={this.clearSaveReminderValue}
            changeModalState={(modal,value)=>this.changeModalState(modal,value)}
            fileHandler={fileHandler}/>
          <SoundLibraryContainer classes={this.state.sidebarState !== SidebarStates.LIBRARY ? "hidden" : ""}
            changeSidebarState={(v)=>this.changeSidebarState(v)}/>
          <AboutContainer
            classes={this.state.sidebarState !== SidebarStates.ABOUT ? "hidden":""}
            changeSidebarState={(v)=>this.changeSidebarState(v)}/>
          <HowToContainer
            videoIndex={this.state.howToVideoIndex}
            classes={this.state.sidebarState !== SidebarStates.HOWTO ? "hidden" : ""}
            changeVideoIndex={(i)=>this.changeHowToVideoIndex(i)}
            changeSidebarState={(v)=>this.changeSidebarState(v)}/>
        </div>
      );
    }

    return (
      <div className="container-fluid no-gutters">
        {modalHandler()}
        <div className="row no-gutters">
          <div className="col-12 col-md-3 col-lg-3 col-xl-2">
            <div className="sidebar-container">
              {sidebar()}
            </div>
          </div>
          <div className="col-12 col-md-9 col-lg-9 col-xl-10">
            {(()=>{
              if(this.state.sidebarState === SidebarStates.HOWTO){
                return (<HowToVideoContainer videoIndex={this.state.howToVideoIndex}/>)
              }
            })()}
            <div className={"soundscape-builder " + (this.state.sidebarState === SidebarStates.HOWTO ? "hidden":"")}>
              <AppHeaderContainer name={this.props.app.name} changeAppVar={(name)=>this.props.changeAppVar("name",name)}/>
              <CollectionContainer addSaveReminderValue={(value)=>this.addSaveReminderValue(value)}/>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) =>{
  return{
    effects: state.effects,
    sounds: state.sounds,
    app: state.app,
    state: state
  }
}

const mapDispatchToProps = (dispatch) =>{
  return{
    unload: () =>{
      dispatch(unloadCollections());
      dispatch(unloadEffects());
      dispatch(unloadSounds());
    },
    add: (collections, effects, sounds) =>{
      dispatch((dispatch)=>{
        dispatch(loadAdditionalSounds(sounds));
        dispatch(loadAdditionalEffects(effects));
        dispatch(loadAdditionalCollections(collections));
      });
    },
    load: (collections, effects, sounds, app) =>{
      dispatch((dispatch)=>{
        dispatch(loadSounds(sounds));
        dispatch(loadEffects(effects));
        dispatch(loadCollections(collections))
        dispatch(loadApp(app));
        dispatch(changeAppVar("loading",false));
      });
    },
    changeAppVar:(varName,value)=>{
      dispatch(changeAppVar(varName,value));
    }
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(App);



