'collection.jsx'
import React, {Component} from 'react';
import EffectContainer from '../containers/EffectContainer';
import CollectionContainerDropTarget from './dragdrop/CollectionContainerDropTarget';
import {SaveReminderValues} from '../constants/constants';

import {isKeypressEnter} from '../Helpers';

class Collection extends Component {
  constructor(props){
    super(props);

    this.state = {
      name: this.props.collection.name,
      editingName : false,
      delayTimer: null,
      soundsPlaying: {},
      areSoundsPlaying: false
    }
    this.changeName = this.changeName.bind(this);
    this.nameInputBlur = this.nameInputBlur.bind(this);
    this.nameInputChange = this.nameInputChange.bind(this);
    this.onDrop = this.onDrop.bind(this);
  }

  componentDidUpdate(prevProps,prevState,snapshot){
    if(prevState.editingName === false && this.state.editingName){
      this.setState({name:this.props.collection.name});
      this.editNameInput.focus();
    }
  }

  componentWillUnmount(){
    window.clearTimeout(this.state.delayTimer);
    this.props.collection.cleanUp();
  }

  onDragEnd(){

  }

  onDrop(payload){
    if(this.props.collection.id === payload.collectionId){return;}
    this.props.collection.moveEffect(payload.collectionId,payload.effectId);
  }

  addSoundPlaying(soundId, callback = function(){}){
    const sound = {[soundId]:1};
    const soundsPlaying = Object.assign({},this.state.soundsPlaying, sound);

    this.setState(prevState =>({
      soundsPlaying:Object.assign({},soundsPlaying,prevState.soundsPlaying),
      areSoundsPlaying:true
    }),()=>{callback()});
  }

  removeSoundPlaying(soundId, callback = function(){}){
    this.setState(function(prevState){
      let soundsPlaying = Object.assign({},prevState.soundsPlaying);
      let areSoundsPlaying = true;
      delete soundsPlaying[soundId];

      if(Object.keys(soundsPlaying).length === 0){
        areSoundsPlaying = false;
      }

      return {
        soundsPlaying: soundsPlaying,
        areSoundsPlaying: areSoundsPlaying
      }
    },()=>{callback()});
  }

  nameInputChange(e){
    this.setState({name:e.target.value});
  }
  nameInputBlur(){
    const delayTimer = window.setTimeout(()=>{
      this.setState({editingName:false});
    },200);
    this.setState({delayTimer:delayTimer});
  }
  changeName(){
    const name = this.editNameInput.value.trim();
    if(name !== ""){
      this.props.collection.changeVar('name', name);
      this.props.addSaveReminderValue(SaveReminderValues.SMALLEDIT);
    }
    this.setState({editingName: false});
  }


  render(){
    const addSoundPlaying = {addSoundPlaying:(soundId)=>this.addSoundPlaying(soundId)};
    const removeSoundPlaying = {removeSoundPlaying:(soundId, callback)=>this.removeSoundPlaying(soundId,callback)};
    const collection = Object.assign({},this.props.collection,addSoundPlaying,removeSoundPlaying);

    const title = () =>{
      if(this.state.editingName){
        return (
          <div>
            <input type="text" onKeyPress={(e)=>isKeypressEnter(e,()=>this.changeName())} ref={(input) => {this.editNameInput = input}} onChange={this.nameInputChange} value={this.state.name} onBlur={this.nameInputBlur}></input>
            <i className="fas fa-check" onClick={this.changeName} onFocus={()=>this.setState({editingName:true})}></i>
          </div>
        )
      }
      return <h5 onClick={()=>this.setState({editingName:true})}>{collection.name}</h5>
    }

    const mediaButtons = () =>{
      if(this.state.areSoundsPlaying){
        return <i onClick={this.props.pauseAll} className="fas fa-pause"></i>;
      }
      else{
        return <i onClick={this.props.playAll} className="fas fa-play"></i>;
      }
    }

    return(
      <div className="collection">
        <div className="collection-head">
          <div className="name">
            {title()}
          </div>
          <div className="play-button">
            {mediaButtons()}
          </div>
          <div className="volume">
            <input type="range" min="0" max="100" value={collection.volume} onChange={(e)=>collection.changeVar("volume",e.target.value)}></input>
          </div>
          <i className="fas fa-times delete" onClick={collection.remove}></i>
        </div>
        <div className="collection-body">
          <CollectionContainerDropTarget onDrop={this.onDrop}>
            <div className={collection.minimized ? "collection-minimized" : ""}>
              <EffectContainer collection={collection} mediaButtonRef={this.props.mediaButtonRef} addSaveReminderValue={(value)=>this.props.addSaveReminderValue(value)}></EffectContainer>
            </div>
          </CollectionContainerDropTarget>
          <div className="collection-minimize-button">
            <i onClick={()=>collection.changeVar("minimized",!collection.minimized)} className={collection.minimized ? "fas fa-chevron-down" : "fas fa-chevron-up"}></i>
          </div>
        </div>
      </div>
    );
  }
}

export default Collection;



