import React from 'react';
import {DropTarget} from 'react-dnd';

export const SoundContainerDropTarget = ({connectDropTarget, children}) =>{
  return connectDropTarget(
    <div className="sound-container-droptarget">
      {children}
    </div>
  )
}

export default DropTarget(
  "DEFAULT",
  {
    drop(props, monitor){
      props.onDrop(monitor.getItem());
    },
  },
  (connect, monitor) => ({
    connectDropTarget: connect.dropTarget()
  }),
)(SoundContainerDropTarget);



