import React, {Component} from 'react';
import {connect} from 'react-redux';
import Controls from '../components/controls';
import {simulateClick} from '../Helpers'
import {changeAppVar} from '../actions/appActions';
import {ModalWindows} from '../constants/constants';

class ControlsContainer extends Component{
  constructor(props){
    super(props);

    this.state = {modalData:null, showModal:false, addData: null};
  }

  render(){
    return(
      <Controls
        loadClicked={()=>simulateClick(this.loadInput)}
        addClicked={()=>simulateClick(this.addInput)}
        exportClicked={this.props.fileHandler.Export}
        changeSidebarState={(val)=>this.props.changeSidebarState(val)}
        openOptionsModal={()=>this.props.changeModalState(ModalWindows.OPTIONS,true)}>

        <input type='file' style={{display:"none"}} onChange={this.props.fileHandler.Load} ref={(ref) => {this.loadInput = ref}}></input>
        <input type='file' style={{display:"none"}} onChange={this.props.fileHandler.Add} ref={(ref) => {this.addInput = ref}}></input>
      </Controls>
    )
  }
}

const mapStateToProps = (state) =>{
  return{
    app: state.app,
    sounds: state.sounds,
    state: state
  };
}
const mapDispatchToProps = (dispatch) =>{
  return{
    changeAppVar: (varName, value)=>{
      dispatch(changeAppVar(varName,value));
    }
  };
}

export default connect(mapStateToProps,mapDispatchToProps)(ControlsContainer);



