import React, {Component} from 'react';

class AddCollectionModal extends Component{
  constructor(props){
    super(props);

    this.getIdState = this.getIdState.bind(this);
    this.buildCollection = this.buildCollection.bind(this);
    this.buildEffects = this.buildEffects.bind(this);
  }

  getIdState(id){
    if(typeof this.props.checkedItems[id] === 'undefined'){
      return false;
    }
    return this.props.checkedItems[id];
  }

  buildCollection(){
    let collection = [];
    const that = this;
    this.props.data.forEach(function(c){
      const checked = that.getIdState(c.id);
      collection.push(
        <li key={c.id}>
          <input type="checkbox" value={c.id} onChange={()=>that.props.toggleChecked(c.id)} checked={checked}/>
          <p>{c.name}</p>
          <ul className="no-style effect-list">
            {that.buildEffects(c.effects, checked)}
          </ul>
        </li>
      )
    });
    return collection;
  }

  buildEffects(effectArray, enabled){
    let effects = [];
    const that = this;
    effectArray.forEach(function(e){
      effects.push(
        <li key={e.id} className={enabled ? "" : "disabled"}>
          <input type="checkbox" value={e.id} onChange={()=>that.props.toggleChecked(e.id)} checked={that.getIdState(e.id)} disabled={!enabled}/>
          <p>{e.name}</p>
        </li>
      )
    });
    return effects;
  }

  render(){
    return(
      <div>
        <ul className="no-style collection-list">
          {this.buildCollection()}
        </ul>
        <button className="mxr-button-accent-solid" onClick={this.props.confirm}>Confirm</button>
        <button className="mxr-button-red-borderless" onClick={this.props.close}>Cancel</button>
      </div>
    )
  }
}

export default AddCollectionModal;



