export function getFileExtension(filename) {
  return filename.substring(filename.lastIndexOf('.') + 1, filename.length);
}

export function blobToFile(theBlob, fileName) {
  var nameArr = fileName.split('/');
  var name = nameArr[nameArr.length - 1];
  var ext = this.getFileExtension(name);

  var newFile = new File([theBlob], name, {
    type: "audio/" + ext
  })
  return newFile;
}

export function getAudioObjName(fileName){
  var nameArr = fileName.split('/');

  return nameArr[nameArr.length - 1].split('.')[0];
}

export function formatAudioObj(blob, fileName, audioArr, newName = null) {
  var nameArr = fileName.split('/');
  var ext = getFileExtension(nameArr[nameArr.length - 1]);
  var name;
  var parent;

  if(newName === null){
    name = nameArr[nameArr.length - 1];
    parent = name.split('.')[0];
  }else{
    name = newName + "." + ext;
    parent = newName;
  }

  var newFile = new File([blob], name, {
    type: "audio/" + ext
  })

  return {[parent]: newFile};
}


export function formatFileName(fileName) {
  let newName = fileName.replace(/_/g, " ");
  let i = newName.split('.');
  i.pop();
  newName = i.join(" ");
  return toTitleCase(newName);
}

export function formatPath(path){
  return toTitleCase(path.replace(/_/g," ").replace(/\//g, " / "));
}

export function toTitleCase(str) {
  let newName = str.replace(/_/g, " ");
  return newName.replace(/\w\S*/g, function(txt) {
    return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();
  });
}

export function objKeysToArr(obj) {
  const arr = [];
  for (var key in obj) {
    arr.push(Object.assign({}, obj[key]));
  }
  return arr;
}

export function objKeyArray(obj){
  const arr = [];
  for (var key in obj) {
    arr.push(key);
  }
  return arr;
}

