export function simulateClick(el) {
  var evt;
  if (document.createEvent) {
    evt = document.createEvent("MouseEvents");
    evt.initMouseEvent("click", true, true, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null);
  }
  (evt) ? el.dispatchEvent(evt): (el.click && el.click());
}

export function getPlayingSounds(effect, sounds){
  return effect.sounds.filter((s) => sounds[s].playing);
}

export function arrayMove(arr, old_index, new_index) {
    if (new_index >= arr.length) {
        var k = new_index - arr.length + 1;
        while (k--) {
            arr.push(undefined);
        }
    }
    arr.splice(new_index, 0, arr.splice(old_index, 1)[0]);
    return arr;
};

export function isKeypressEnter(e, callback = function(){}){
  const code = (e.keyCode ? e.keyCode : e.which);
  console.log(code);
  if(code === 13){
    callback();
    return true;
  }
  return false;
}

export function isObjEmpty(obj){
  return (Object.keys(obj).length && obj.constructor === Object);
}

export function soundExists(id, sounds){
  return (typeof sounds[id] !== 'undefined');
}



