import React, {Component} from 'react';
import SaveReminderModal from '../components/saveReminderModal';

class SaveReminderModalContainer extends Component{
  render(){
    return(
      <SaveReminderModal confirm={this.props.confirm} close={this.props.close}/>
    )
  }
}

export default SaveReminderModalContainer;



