import JSZip from 'jszip';
import FileSaver from 'file-saver';
import {objKeysToArr, objKeyArray, getFileExtension, formatAudioObj, getAudioObjName} from '../Helpers'
import UUID from 'uuid/v4';

export function loadData(e,callbacks, state = null){
  const file = e.target.files[0];
  if(typeof file === 'undefined'){return;}

  processFile(file,callbacks,state);
}

function processFile(file, callbacks, state = null){
  let fileObj = {};
  JSZip.loadAsync(file).then((zip)=>{
    try{
      //Find meta.json inside the file. If no meta.json, catch the error
      zip.file('meta.json').async('string').then((content) =>{
        //Extract the json as an obj
        const meta = JSON.parse(content);
        //Populate variables with the key values of the
        fileObj.collections = varifyObject(meta.collections)
        fileObj.effects = varifyObject(meta.effects);
        fileObj.sounds = varifyObject(meta.sounds);
        fileObj.app = varifyObject(meta.app);
        fileObj.changedSoundIds = null;

        //Call the loading callback
        callbacks.onLoad();

        if(state !== null){
          fileObj = compareAgainstState(fileObj,state);
        }

        //Next stage
        getAudio(zip, fileObj, callbacks);
      });
    }
    //If the file is an archive, but does not contain a meta.json
    catch(e){
      alert("No meta.json file was found in the archive");
    }
  })
  //If the file isn't an archive
  .catch((error)=>{
    alert("Please use a .immerse file");
  });
}

function compareAgainstState(fileObj, state){
  const collectionArray = objKeyArray(fileObj.collections);
  const effectArray = objKeyArray(fileObj.effects);
  const soundArray = objKeyArray(fileObj.sounds);

  //Test collections first. Any non-undefined means a conflict
  collectionArray.forEach((id, i)=>{
    if(typeof state.collections[id] !== 'undefined'){
      //Conflict, define new UUID, and apply new UUID to collection, and any child effects
      const oldID = id;
      const newID = UUID();
      collectionArray[i] = newID;
      fileObj.collections[newID] = Object.assign({},fileObj.collections[oldID]);
      fileObj.collections[newID].id = newID;
      delete fileObj.collections[oldID];

      // fileObj.collections[newID].effects.forEach((effectID)=>{
      //
      // });
    }
  });

  effectArray.forEach((id,i)=>{
    if(typeof state.effects[id] !== 'undefined'){
      const oldID = id;
      const newID = UUID();
      effectArray[i] = newID;
      fileObj.effects[newID] = Object.assign({},fileObj.effects[oldID]);
      fileObj.effects[newID].id = newID;
      delete fileObj.effects[oldID];


      collectionArray.forEach((collectionID)=>{
        fileObj.collections[collectionID].effects.forEach((eID,eI)=>{
          if(eID === oldID){
            fileObj.collections[collectionID].effects[eI] = newID;
            return;
          }
        });
      });

      fileObj.effects[newID].sounds.forEach((soundID)=>{
        fileObj.sounds[soundID].effectId = newID;
      });
    }
  });

  soundArray.forEach((id,i)=>{
    if(typeof state.sounds[id] !== 'undefined'){
      const oldID = id;
      const newID = UUID();

      if(fileObj.changedSoundIds === null){fileObj.changedSoundIds = {};}
      fileObj.changedSoundIds[oldID] = newID;

      soundArray[i] = newID;
      fileObj.sounds[newID] = Object.assign({},fileObj.sounds[oldID]);
      fileObj.sounds[newID].id = newID;
      delete fileObj.sounds[oldID];

      effectArray.forEach((effectID)=>{
        fileObj.effects[effectID].sounds.forEach((sID,sI)=>{
          if(sID === oldID){
            fileObj.effects[effectID].sounds[sI] = newID;
            return;
          }
        });
      });
    }
  });

  return fileObj;
}

function getAudio(zip, fileObj, callbacks){
  let audio = {};
  let fileTotal = Object.keys(zip.files).length;
  let fileCount = 0;

  //Loops through the zip file
  zip.forEach((path,entry)=>{
    if(entry.dir){
      fileCount++;
      return;
    }
    zip.file(path).async('blob').then(function success(content){
      let newName = null;
      fileCount++;
      if(entry.name === 'meta.json'){return;}

      if(fileObj.changedSoundIds !== null){
        if(typeof fileObj.changedSoundIds[getAudioObjName(entry.name)] !== 'undefined'){
          console.log("MATCH FOUND");
          newName = fileObj.changedSoundIds[getAudioObjName(entry.name)];
        }
      }

      let audioObj = formatAudioObj(content, entry.name, audio, newName)

      Object.assign(audio,audioObj);
      if(fileTotal === fileCount){
        finish(audio, fileObj,callbacks);
      }
    }, function error(e){
      alert(e);
      callbacks.onFailure();
    });
  });
}

function finish(audio, fileObj, callbacks){
  const audioArr = objKeyArray(audio);
  const blob = window.URL || window.webkitURL;
  audioArr.forEach((s) =>{
    fileObj.sounds[s].url = blob.createObjectURL(audio[s]);
    fileObj.sounds[s].file = audio[s];
    fileObj.sounds[s].playing = false;
  });
  const state = {collections:fileObj.collections,
    effects:fileObj.effects,
    sounds:fileObj.sounds,
    app:fileObj.app};
  callbacks.onSuccess(state);
}
const varifyObject = (item) =>{
  if(typeof item === 'object'){
    return item;
  }
  return {};
}

//Exports the state as an archive with added meta.json file
export function exportData(state, callback=null){
  const str = JSON.stringify(state);
  const file = new File([str], 'meta.json', {type: "text/plain;charset=utf-8"});
  const sounds = Object.assign({},state.sounds);
  let soundArr = objKeysToArr(sounds);
  const effects = state.effects;
  const app = state.app;
  const name = app.name + ".immerse"

  let effectSounds = {};
  for(var key in effects){
    effects[key].sounds.forEach((id)=>{
      effectSounds[id] = true;
    });
  }
  for(let i = soundArr.length - 1; i >= 0; i--){
    if(typeof effectSounds[soundArr[i].id] === 'undefined'){
      soundArr.splice(i,1);
    }
  }

  let zip = new JSZip();

  zip.file("meta.json", file);

  soundArr.forEach((sound)=>{
    zip.folder("sounds")
    .file(sound.id + '.' + getFileExtension(sound.file.name).toLowerCase(), sound.file)
  });

  zip.generateAsync({
    type: "blob",
    compression: "DEFLATE",
    compressionOptions: {
      level: 9
    }
  }).then((content)=>{
    FileSaver.saveAs(content,name);
  });
}



