import React, {Component} from 'react';
import AddCollectionModal from '../components/addCollectionModal';

class AddCollectionModalContainer extends Component{
  constructor(props){
    super(props);
    this.buildModalData = this.buildModalData.bind(this);
    this.buildCheckedItemsState = this.buildCheckedItemsState.bind(this);
    this.toggleChecked = this.toggleChecked.bind(this);
    this.confirm = this.confirm.bind(this);
    this.state = {
      data: [],
      checkedItems: {}
    }
  }

  componentDidMount(){
    this.buildModalData(this.props.data);
  }

  buildModalData(added){
    const collections = added.collections;
    const collectionIdArray = Object.keys(collections);
    const effects = added.effects;

    let data = [];

    collectionIdArray.forEach(function(collectionId){
      let collectionData = {id:collectionId,name:collections[collectionId].name};
      let collectionEffects = [];
      collections[collectionId].effects.forEach(function(effectId){
        collectionEffects.push({id:effectId,name:effects[effectId].name});
      });

      collectionData.effects = collectionEffects;
      data.push(collectionData);
    });

    this.setState({data:data});

    this.buildCheckedItemsState(data);
  }

  buildCheckedItemsState(data){
    let items = {};
    data.forEach(function(i){
      items[i.id] = true;
      i.effects.forEach(function(i){
        items[i.id] = true;
      });
    });
    this.setState({checkedItems:items});
  }

  toggleChecked(id){
    if(typeof this.state.checkedItems[id] === 'undefined'){return;}

    let items = Object.assign({},this.state.checkedItems);
    items[id] = !items[id];

    this.setState({checkedItems:items});
  }

  confirm(){
    const {checkedItems} = this.state;
    const that = this;

    let collectionObj = Object.assign({},this.props.data.collections);
    const collectionArr = Object.entries(collectionObj).splice(0);

    let effectObj = Object.assign({},this.props.data.effects);
    const effectArr = Object.entries(effectObj);
    let effectRemoveArr = [];

    let soundObj = Object.assign({},this.props.data.sounds);

    effectArr.forEach(function(e){
      if(!checkedItems[e[0]]){
        that.removeAddedEffect(e[0],effectObj, soundObj);
        effectRemoveArr.push(e[0]);
      }
    });

    collectionArr.forEach(function(c, ci){
      for(let ei = effectRemoveArr.length - 1; ei >= 0; ei--){
        const previousCount = collectionObj[c[0]].effects.length;
        console.log(previousCount);
        collectionObj[c[0]].effects = collectionObj[c[0]].effects.filter(id => id !== effectRemoveArr[ei]);
      }
      if(!checkedItems[c[0]]){
        that.removeAddedCollection(c[0], collectionObj, effectObj, soundObj);
      }
    });

    this.props.confirm({c:collectionObj,e:effectObj,s:soundObj});
  }

  removeAddedCollection(id, cObj, eObj, sObj){
    const that = this;
    const effects = cObj[id].effects;
    effects.forEach(function(e){
      that.removeAddedEffect(e, eObj, sObj)
    });
    delete cObj[id];
  }
  removeAddedEffect(id, eObj, sObj, data){
    const that = this;
    const sounds = eObj[id].sounds;
    sounds.forEach(function(s){
      that.removeAddedSound(s, sObj);
    });

    delete eObj[id];
  }
  removeAddedSound(id, sObj){
    delete sObj[id];
  }

  render(){
    return(
      <AddCollectionModal data={this.state.data} toggleChecked={this.toggleChecked} checkedItems={this.state.checkedItems} close={this.props.close} confirm={this.confirm}/>
    )
  }
}

export default AddCollectionModalContainer;



