import * as types from '../actions/effectsConsts';
import {arrayMove} from '../Helpers';

export default function reducer(state={}, action){
  const {payload} = action;

  switch(action.type){
    case types.LOAD_EFFECTS:{
      return payload.effects;
    }
    case types.LOAD_ADD_EFFECTS:{
      return Object.assign({},payload.effects, state);
    }
    case types.UNLOAD_EFFECTS:{
      return {};
    }
    case types.CREATE_EFFECT:{
      const {effect} = payload;
      return {...state, [effect.id]:effect}
    }
    case types.REMOVE_EFFECT:{
      const {effectId} = payload;
      const effects = Object.assign({}, state);
      delete effects[effectId];
      return effects;
    }
    case types.REMOVE_EFFECTS:{
      const effectsToRemove = payload.effects;
      const effects = Object.assign({},state);
      effectsToRemove.forEach((e)=>{
        delete effects[e];
      });

      return effects;
    }
    case types.ADD_SOUND_TO_EFFECT:{
      const {effectId, soundId} = payload;
      const sounds = state[effectId].sounds.concat(soundId);
      return updateState(effectId, 'sounds', sounds);
    }
    case types.MOVE_SOUND:{
      const {effectId, oldIndex, newIndex} = payload;
      const sounds = state[effectId].sounds.slice();
      console.log(sounds);
      return updateState(effectId, 'sounds', arrayMove(sounds, oldIndex, newIndex));
    }
    case types.REMOVE_SOUND_REFERENCE:{
      const {effectId, soundId} = payload;
      const sounds = state[effectId].sounds.filter(s => s !== soundId);
      return updateState(effectId, 'sounds', sounds);
    }
    case types.ADD_SOUND_REFERENCE:{
      const {effectId, soundId, index} = payload;
      const sounds = state[effectId].sounds.slice();
      sounds.splice(index, 0 , soundId);
      return updateState(effectId, 'sounds', sounds);
    }
    case types.CHANGE_EFFECT_VAR:{
      const {effectId, varName, value} = payload;
      return updateState(effectId, varName, value);
    }
    default: {return state}
  }

  function updateState(effectId, varName, value){
    return{
      ...state,
      [effectId]: Object.assign({}, state[effectId],{[varName]:value})
    }
  }
}



