import * as types from './appConsts';
import {appGeneric} from './appConsts';
import {objNormalize} from '../js/obj_processing';

export function loadApp(app){
  return{
    type: types.LOAD_APP,
    payload: objNormalize(app,appGeneric())
  }
}

export function changeAppVar(varName,value){
  return{
    type: types.CHANGE_APP_VAR,
    payload:{
      varName,
      value
    }
  }
}



