import React from 'react';
import {DragSource} from 'react-dnd';
import DragTargets from './DragTargets';

export const SoundLibraryDragTarget = ({connectDragSource, children, classes}) =>{

  return connectDragSource(
    <div className={classes}>
      {children}
    </div>
  );
}

export default DragSource(
  "DEFAULT",
  {
    beginDrag: props =>({type:DragTargets.LIBRARY_DRAG_TARGET ,name:props.file.name, path:props.file.path})
  },
  (connect, monitor) => ({
    connectDragSource: connect.dragSource(),
  }),
)(SoundLibraryDragTarget);



