import React, {Component} from 'react';

class OptionsModal extends Component{
  render(){
    return(
      <div>
        <p>Hello Friend It Is I The Options Modal</p>
        <button className="mxr-button-accent-solid" onClick={this.props.confirm}>Done</button>
      </div>
    )
  }
}

export default OptionsModal;



