import React, {Component} from 'react';
import {SidebarStates} from '../constants/constants'

class Controls extends Component{
  render(){
    return(
      <div className="sidebar sidebar-main dark ">
        <div className="sidebar-background"></div>
        <div className="sidebar-head">
          <h5>Sound Mixer</h5>
        </div>
        <div className="sidebar-content no-padding">
          <ul className="no-style">
            <li>
              <span>App</span>
            </li>
            <li>
              <button type="button" name="button" onClick={this.props.loadClicked}>
                <i className="fas fa-upload"></i>
                Load
              </button>
            </li>
            <li>
              <button type="button" name="button" onClick={this.props.addClicked}>
                <i className="fas fa-plus"></i>
                Add
              </button>
            </li>
            <li>
              <button type="button" name="button" onClick={this.props.exportClicked}>
                <i className="fas fa-download"></i>
                Export
              </button>
            </li>
            <li>
              <button type="button" name="button" onClick={()=>{}} className="disabled">
                <i className="fas fa-box-open"></i>
                Soundscape Collection
              </button>
            </li>
            <li>
              <button type="button" name="button" onClick={this.props.openOptionsModal}>
                <i className="fas fa-cog"></i>
                Options
              </button>
            </li>
            <li>
              <span>Soundscapes</span>
            </li>
            <li>
              <button type="button" name="button" onClick={()=>this.props.changeSidebarState(SidebarStates.LIBRARY)}>
                <i className="fas fa-folder"></i>
                Sound Library
              </button>
            </li>
            <li>
              <span>Info</span>
            </li>
            <li>
              <button type="button" name="button" onClick={()=>this.props.changeSidebarState(SidebarStates.HOWTO)}>
                <i className="fas fa-question"></i>
                How To Use
              </button>
            </li>
            <li>
              <button type="button" name="button" onClick={()=>this.props.changeSidebarState(SidebarStates.ABOUT)}>
                <i className="fas fa-book-open"></i>
                About
              </button>
            </li>
            <li>
              <span>Donate</span>
            </li>
            <li>
            <button type="button" name="button" className="disabled">
              <i className="fab fa-patreon"></i>
              Patreon
            </button>
            </li>
            <li>
              <button type="button" name="button" className="disabled">
              <i className="fab fa-paypal"></i>
              Paypal
              </button>
            </li>
          </ul>
        </div>
        {this.props.children}
      </div>
    )
  }
}

export default Controls;



