'soundsConsts.js'
import UUID from 'uuid/v4'
import {formatFileName, getFileExtension} from '../Helpers'

export const LOAD_SOUNDS = "LOAD_SOUNDS";
export const LOAD_ADD_SOUNDS = "LOAD_ADD_SOUNDS";
export const CREATE_SOUND = "CREATE_SOUND";
export const REPLACE_SOUND = "REPLACE_SOUND";
export const MOVE_SOUND = "MOVE_SOUND";
export const REMOVE_SOUND = "REMOVE_SOUND";
export const REMOVE_SOUNDS = "REMOVE_SOUNDS";
export const CHANGE_SOUND_VAR = "CHANGE_SOUND_VAR";
export const UNLOAD_SOUNDS = "UNLOAD_SOUNDS";

export function soundGeneric(){
  return {
    id: "",
    effectId: "",
    file: "",
    fileName: "",
    name: "",
    ext: "",
    url: "",
    loop: false,
    volume: 100,
    mute: false,
    fading: false
  };
}

export function soundNormalize(sound){
  return Object.assign({},soundGeneric(),sound);
}

export function soundBuilder(effectId, file, fileURL){
  let sound = soundGeneric();
  sound.id = UUID();
  sound.effectId = effectId;
  sound.file = file;
  sound.fileName = file.name;
  sound.name = formatFileName(file.name);
  sound.ext = getFileExtension(file.name);
  sound.url = fileURL;

  return sound;
}



