import * as types from './soundsConsts';
import {soundGeneric} from './soundsConsts';
import {objNormalize} from '../js/obj_processing';
import {objKeysToArr} from '../Helpers';

export function loadSounds(sounds){
  return{
    type: types.LOAD_SOUNDS,
    payload: {sounds: objNormalize(objKeysToArr(sounds),soundGeneric())}
  }
}
export function loadAdditionalSounds(sounds){
  return{
    type: types.LOAD_ADD_SOUNDS,
    payload: {sounds: objNormalize(objKeysToArr(sounds),soundGeneric())}
  }
}
export function createSound(sound){
  return{
    type: types.CREATE_SOUND,
    payload: {sound}
  }
}

export function replaceSound(soundId, file, fileUrl){
  return{
    type: types.REPLACE_SOUND,
    payload: {
      soundId,
      file,
      fileUrl
    }
  }
}

export function moveSound(effectId, soundId, index){
  return{
    type: types.MOVE_SOUND,
    payload: {
      effectId,
      soundId,
      index
    }
  }
}

export function removeSound(soundId){
  return{
    type: types.REMOVE_SOUND,
    payload: {soundId}
  }
}

export function removeSounds(sounds){
  return{
    type: types.REMOVE_SOUNDS,
    payload: {sounds}
  }
}

export function unloadSounds(){
  return{
    type: types.UNLOAD_SOUNDS,
    payload: {}
  }
}

export function changeSoundVar(soundId, varName, value, extras = null){
  return{
    type: types.CHANGE_SOUND_VAR,
    payload:{
      soundId,
      varName,
      value,
      extras
    }
  }
}



