import React, {Component} from 'react';

class AppHeader extends Component{
  render(){
    const name = () =>{
      if(this.props.editingName){
        return(
          <div className="app-name-input">
            <input type="text" onKeyPress={(e)=>this.props.nameInputKeyPress(e)} ref={this.props.nameInputRef} onChange={this.props.nameInputChanged} value={this.props.name} onBlur={this.props.nameInputBlur}></input>
            <i className="fas fa-check" onClick={this.props.changeName} onFocus={()=>this.props.nameClicked}></i>
          </div>
        );
      }
      return(<h5 onClick={this.props.nameClicked}>{this.props.name}</h5>);
    }
    return(
      <div className="builder-head">
        {name()}
      </div>
    )
  }
}

export default AppHeader;



