export function volumeSliderStyle(vol, light = "#f6277a", dark = "#4a031f"){
  const min = vol;
  const max = parseInt(min, 10) + 1;
  const c1 = "#38CCC7";
  const c2 = "#38CCC7";
  const grad = `linear-gradient(to right, ${c1}, ${c2} ${min}%, rgba(0,0,0,.2) ${max}%, rgba(0,0,0,.2) 100%)`;
  const gradWebkit = `-webkit-linear-gradient(left, ${c1}, ${c2} ${min}%, rgba(0,0,0,.2) ${max}%, rgba(0,0,0,.2) 100%)`;
  const gradMoz = `-moz-linear-gradient(left, ${c1}, ${c2} ${min}%, rgba(0,0,0,.2) ${max}%, rgba(0,0,0,.2) 100%)`;
  const gradFallback = "#fafafa";
  return {
    backgroundSize: '100% 5px',
    backgroundPosition: 'center',
    backgroundRepeat: 'no-repeat',
    background: gradFallback,
    background: gradMoz,
    background: gradWebkit,
    background: grad
  };
}



