import React, {Component} from 'react';
import SoundLibraryDragTarget from './dragdrop/SoundLibraryDragTarget';
import {toTitleCase, formatPath} from '../Helpers';

class SoundLibrary extends Component{
  render(){
    const {sounds, opened, folderClicked, playing, filePath, stopPlaying} = this.props;
    const {head, tree} = sounds;

    // Builds the sound-library list
    const fileList = () =>{
      // The final returned array of elements
      let structure = [];

      // Will run first, each item in the 'head' array is a key for a value in the 'tree' object
      // This function should only run once when building the library.
      const buildHead = () =>{
        // Loops through the 'head' array, each item in it is item in the library
        head.forEach((item)=>{
          // An HTML element is pushed to the 'structure' array.
          structure.push(
            <li key={item} className={typeof opened[item] !== 'undefined' ? "open" : ""}>
              <div className="folder" onClick={()=>folderClicked(item)}>
                <span className="item-name">
                  <i className={typeof opened[item] !== 'undefined' ? "fas fa-folder-open" : "fas fa-folder"}></i>
                  <p>{tree[item].name}</p>
                </span>
                <span className="item-chevron">
                  <i className={typeof opened[item] !== 'undefined' ? "fas fa-angle-down" : "fas fa-angle-right"}></i>
                </span>
              </div>
              <ul className="no-style">
                {typeof opened[item] !== 'undefined' &&
                // If 'item' as a key (Which is the file path) exists in the 'opened' object, than that folder has been opened, and its children will be rendered
                formatChildren(tree[item])}
              </ul>
            </li>
          )
        });
      }
      // Handles the formatting of folder children. Children can either be more folders, of sound files.
      function formatChildren(parent){
        let subStructure = [];
        // 'children' and 'soundArray' can be undefined. This means the parent does not contain the assosciated items.
        const children = parent.children;
        const soundArray = parent.sounds;

        // If 'children' is not undefined, than the parent contains more folders, and a folder is built for each child.
        if(typeof children !== 'undefined' && children.length){
          children.forEach((child)=>{
            subStructure.push(buildFolder(tree[child]));
          });
        }
        // If 'sounds' is not undefined, than the parent contains sound files, and a file is built for each sound.
        if(typeof soundArray !== 'undefined' && soundArray.length){
          subStructure = buildFile(soundArray,subStructure);
        }
        return subStructure;
      }

      // A folder that contains children is built and returned here
      const buildFolder = (folder) =>{
        return (
          <li key={folder.path} className={typeof opened[folder.path] !== 'undefined' ? "open" : ""}>
            <div className="folder" onClick={()=>folderClicked(folder.path)}>
              <span className="item-name">
                <i className={typeof opened[folder.path] !== 'undefined' ? "fas fa-folder-open" : "fas fa-folder"}></i>
                <p>{folder.name}</p>
              </span>
              <span className="item-chevron">
                <i className={typeof opened[folder.path] !== 'undefined' ? "fas fa-angle-down" : "fas fa-angle-right"}></i>
              </span>
            </div>
            <ul className="no-style">
              {typeof opened[folder.path] !== 'undefined' &&
              formatChildren(folder)}
            </ul>
          </li>
        );
      }

      // A file representing a sound file is built and returned here
      const buildFile = (soundArray, subStructure) =>{
        let currentCategory = null;
        soundArray.forEach((sound)=>{
          const file = tree[sound.path];
          if(currentCategory !== file.category){
            subStructure.push(<li key={file.path + "-c-" + file.category} className={"category c-" + file.category}><p>{toTitleCase(file.category)}</p></li>);
            currentCategory = file.category;
          }
          subStructure.push(
            <SoundLibraryDragTarget file={file} classes="" key={file.path}>
              <li onClick={()=>this.props.soundClicked(file)}>
                <div className={"file c-" + file.category + (playing && file.path === filePath ? " playing": "")}>
                  <span className="item-name">
                    <i className="fas fa-volume-off"></i>
                    <p>{file.name}</p>
                  </span>
                </div>
              </li>
            </SoundLibraryDragTarget>
          );
        });
        return subStructure;
      }

      buildHead();


      return(
        <ul className="no-style library-list">
        {structure}
        </ul>
      );
    }

    const currentlyPlaying = () =>{
      if(playing){
        const sound = sounds.tree[filePath];
        console.log(sound);
        return (
          <div className="currently-playing">
            <div className="head">
              <p>Previewing: {formatPath(sound.parent)}</p>
              <h5>{sound.name}</h5>
            </div>
            <i className="fas fa-times" onClick={stopPlaying}></i>
          </div>
        );
      }
      return(
        <div className="currently-playing">
          <div className="head">
            <p>Previewing: Nothing</p>
          </div>
        </div>
      )
    }

    return(
      <div className={"sidebar sidebar-library dark " + this.props.classes}>
        <div className="sidebar-head">
          <button type="button" name="button" className="back" onClick={this.props.sidebarMain}><i className="fas fa-angle-left"></i></button>
          <h5>Sound Library</h5>
        </div>
        {currentlyPlaying()}
        <div className="sidebar-content no-padding">
          {fileList()}
        </div>
      </div>
    )
  }
}
export default SoundLibrary;



