import React from 'react';
import {DragSource} from 'react-dnd';
import DragTargets from './DragTargets';

export const SoundDragTarget = ({connectDragSource, children, classes}) =>{
  return connectDragSource(
    <div className={classes}>
      {children}
    </div>
  );
}

export default DragSource(
  "DEFAULT",
  {
    beginDrag: props =>({type:DragTargets.SOUND_DRAG_TARGET, soundId:props.soundId, effectId:props.effectId})
  },
  (connect, monitor) => ({
    connectDragSource: connect.dragSource(),
    isDragging: monitor.isDragging(),
  }),
)(SoundDragTarget);



