import * as types from '../actions/appConsts';
import {appBuilder} from '../actions/appConsts';

export default function reducer(state=appBuilder(),action){
  const {payload} = action;

  switch(action.type){
    case types.LOAD_APP:{
      return payload;
    }
    case types.CHANGE_APP_VAR:{
      const {varName,value} = payload;
      return updateState(varName,value);
    }
    default:{return state}
  }

  function updateState(varName,value){
    return Object.assign({},state,{[varName]:value});
  }
}



