import * as types from '../actions/soundsConsts';
import {soundGeneric} from '../actions/soundsConsts';
import {formatFileName, getFileExtension} from '../Helpers'

export default function reducer(state={}, action){
  const {payload} = action;
  switch(action.type){
    //Case for loading sounds from file
    case types.LOAD_SOUNDS:{
      //Payload is an object containing key-object pairs
      //To allow for older files with older values to be loaded
      //The loaded sound object is copied into a generic template
      const sound = soundGeneric();
      let sounds = {};
      //Loop through the objects and combines each with a generic template
      for(let id in payload.sounds){
        sounds[id] = Object.assign({},sound,payload.sounds[id]);
      }
      return sounds;
    }
    case types.LOAD_ADD_SOUNDS:{
      const sound = soundGeneric();
      let sounds = {};
      //Loop through the objects and combines each with a generic template
      for(let id in payload.sounds){
        sounds[id] = Object.assign({},sound,payload.sounds[id]);
      }
      return Object.assign({},sounds,state);
    }
    //Case for creating a new sound from a loaded audio file
    case types.CREATE_SOUND:{
      //Adds a key-object pair to the state
      return {...state, [payload.sound.id]:payload.sound}
    }
    //Case for replacing a currently loaded sound file with a new one
    case types.REPLACE_SOUND:{
      const {soundId, file, fileUrl} = action.payload;
      return{
        ...state,
        [soundId]: Object.assign({}, state[soundId], {
          file,
          url:fileUrl,
          fileName: file.name,
          name: formatFileName(file.name),
          ext: getFileExtension(file.name)
        })
      }
    }
    //Case for removing a sound
    case types.REMOVE_SOUND:{
      const sounds = Object.assign({}, state);
      delete sounds[payload.soundId];
      return sounds;
    }
    case types.REMOVE_SOUNDS:{
      const s = payload.sounds;
      const sounds = Object.assign({}, state);
      s.forEach((sound)=>{
        console.log("Deleting " + sound);
        delete sounds[sound];
      });
      return sounds;
    }
    case types.UNLOAD_SOUNDS: {
      return {};
    }
    //Generic case for changing any variable in the sound
    case types.CHANGE_SOUND_VAR:{
      const {soundId, varName, value} = action.payload;
      if(!idExists(state,soundId)){return state;}
      return updateState(soundId,varName,value);
    }
    default: {return state}
  }

  //Helper function for updating the state with arbitary variable
  function updateState(id, varName, value){
    return{
      ...state,
      [id]: Object.assign({}, state[id], {[varName]:value})
    }
  }
  function idExists(state,id){
    if(typeof state[id] === 'undefined'){
      return false;
    }
    return true;
  }
}



