import React from 'react'
import {DragSource} from 'react-dnd';
import DragTargets from './DragTargets';

export const EffectDragSource = ({connectDragSource, children, classes}) =>{
  return connectDragSource(
    <div className={classes}>
      {children}
    </div>
  );
}

export default DragSource(
  "DEFAULT",
  {
    beginDrag: props =>({type:DragTargets.EFFECT_DRAG_SOURCE, effectId: props.effectId, collectionId: props.collectionId})
  },
  (connect, monitor) =>({
    connectDragSource: connect.dragSource()
  })
)(EffectDragSource);



